function TransitionComponent(props: TransitionProps) {
  const style = useSpring({
    from: {
      opacity: 0,
      transform: "translate3d(20px,0,0)",
    },
    to: {
      opacity: props.in ? 1 : 0,
      transform: `translate3d(${props.in ? 0 : 20}px,0,0)`,
    },
  });

  return (
    <animated.div style={style}>
      <Collapse {...props} />
    </animated.div>
  );
}

const StyledTreeItem = styled((props: TreeItemProps) => (
  <TreeItem {...props} TransitionComponent={TransitionComponent} />
))(({ theme }) => ({
  [`& .${treeItemClasses.iconContainer}`]: {
    "& .close": {
      opacity: 0.3,
    },
  },
  [`& .${treeItemClasses.group}`]: {
    marginLeft: 15,
    paddingLeft: 18,
    borderLeft: `1px dashed ${alpha(theme.palette.text.primary, 0.4)}`,
  },
}));

export default function CustomizedTreeView() {
  const nodes = [];

  for (let i = 0; i < Math.floor(Math.random() * 100) + 25; i++) {
    nodes.push(i);
  }

  return (
    <TreeView
      aria-label="customized"
      defaultExpanded={["1", "99"]}
      defaultCollapseIcon={<MinusSquare />}
      defaultExpandIcon={<PlusSquare />}
      defaultEndIcon={<CloseSquare />}
      sx={{ height: "100%", flexGrow: 1, maxWidth: 400, overflowY: "auto" }}
    >
      <StyledTreeItem nodeId="1" label="Main menu items">
        <StyledTreeItem nodeId="2" label="Hello" />
        <StyledTreeItem nodeId="3" label="Subtree with children">
          <StyledTreeItem nodeId="6" label="Hello" />
          <StyledTreeItem nodeId="7" label="Sub-subtree with children">
            <StyledTreeItem nodeId="9" label="Child 1" />
            <StyledTreeItem nodeId="10" label="Child 2" />
            <StyledTreeItem nodeId="11" label="Child 3" />
          </StyledTreeItem>
          <StyledTreeItem nodeId="8" label="Hello" />
        </StyledTreeItem>
        <StyledTreeItem nodeId="4" label="World" />
        <StyledTreeItem nodeId="5" label="Something something" />

        {nodes.map((node) => (
          <StyledTreeItem
            nodeId={`${node}-id`}
            label={" ASB menu item" + node}
          />
        ))}
      </StyledTreeItem>

      <StyledTreeItem nodeId="99" label="new menu">
        <StyledTreeItem nodeId="2" label="Countries" />
        <StyledTreeItem nodeId="3" label="Countries with children">
          {nodes.map((node) => (
            <StyledTreeItem
              nodeId={`${node}-id`}
              label={" sub menu ASB menu item" + node}
            />
          ))}
        </StyledTreeItem>
        <StyledTreeItem nodeId="4" label="World" />
        <StyledTreeItem nodeId="2" label="India" />
        <StyledTreeItem nodeId="32" label="Australia" />
        <StyledTreeItem nodeId="f2" label="NewZealnd" />
        <StyledTreeItem nodeId="e2" label="US" />
        <StyledTreeItem nodeId="f2" label="UK" />
        <StyledTreeItem nodeId="ff2" label="Russia" />
        <StyledTreeItem nodeId="sss2" label="China" />
        <StyledTreeItem nodeId="fffd2" label="Swiss" />
        <StyledTreeItem nodeId="222" label="Germany" />
        <StyledTreeItem nodeId="aa2" label="Italy" />
        <StyledTreeItem nodeId="asdas2" label="Spain" />
      </StyledTreeItem>
    </TreeView>
  );
}
